.dashboard{
    width: 100vw;
    max-width: 100%;
    display: grid;
    grid-template-columns: 1fr 5fr;
    position: absolute;
}

.dashboardContainer{
    border-left: 2px solid rgba(0,0,0,0.13);
    background-color: white;
    padding: 3rem 0;
}

.dashboardContainer > h1{
    color: rgba(0,0,0,0.733);
    font: 300 2rem "Roboto";
    margin: auto;
    padding: 1.5rem;
    width: 50%;
    text-align: center;
}

.dashboardSummary{
    margin: 2rem 0;
}

.dashboardSummary > div{
    display: flex;
    justify-content: center;
    background-color: white;
}

.dashboardSummary > div > p{
    background-color: rgba(70, 117, 218, 0.932);
    color: white;
    width: 100%;
    margin: 0.2rem;
    padding: 1.5rem;
    text-align: center;
    font: 300 1.3rem "Roboto";
}

.dashboardSummaryBox2 > a{
    color: rgb(0,0,0);
    font: 300 2rem "Roboto";
    text-align: center;
    background-color: rgb(255, 233, 174);
    text-decoration: none;
    padding: 1.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 100%;
    margin: 2rem;
    width: 10vmax;
    height: 10vmax;
}

.dashboardSummaryBox2 > a:first-child{
    background-color: rgb(255, 110, 110);
    color: white;
}

.dashboardSummaryBox2 > a:last-child{
    background-color: rgb(51, 51, 51);
    color: white;
}

.lineChart{
    width: 80%;
    margin: auto;
}

.doughnutChart{
    width: 30vmax;
    margin: auto;
}

@media screen and (max-width: 600px){
    .dashboard{
        grid-template-columns: 1fr;
    }

    .dashboardContainer{
        border-left: none;
    }

    .dashboardSummary > div > p{
        margin: 0;
    }

    .dashboardSummaryBox2 > a{
        padding: 0.5rem;
        margin: 1rem;
        font: 300 0.9rem "Roboto";
    }
}