.emptyCart{
    margin: auto;
    text-align: center;
    padding: 10vmax;
    height: 50vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.emptyCart > svg{
    color: tomato;
    font-size: 5vmax;
}

.emptyCart > p{
    font-size: 2vmax;
}

.emptyCart > a{
    background-color: rgb(51, 51, 51);
    font: 400 1vmax "Roboto";
    padding: 1vmax 3vmax;
    border: none;
    color: white;
    text-decoration: none;
    cursor: pointer;
    transition: 0.5s;
}

.emptyCart > a:hover{
    background-color: rgb(51, 51, 51, 0.2);
    color: black;
}

.cartPage{
    padding: 5vmax;
}
.cartHeader{
    background-color: tomato;
    width: 90%;
    box-sizing: border-box;
    margin: auto;
    color: white;
    display: grid;
    grid-template-columns: 4fr 1fr 1fr;
    font: 300 0.7vmax "Robot";
}

.cartHeader > p{
    margin: 10px;
}

.cartHeader > p:last-child{
    text-align: end;
}

.cartContainer{
    width: 90%;
    margin: auto;
    display: grid;
    grid-template-columns: 4fr 1fr 1fr;
}

.cartInput{
    display: flex;
    height: 8vmax;
    align-items: center;
}

.cartInput > button{
    border: none;
    background-color: rgba(0,0,0,0.616);
    padding: 0.5vmax;
    cursor: pointer;
    color: white;
    transition: all 0.5s;
}

.cartInput > button:hover{
    background-color: rgba(0,0,0,0.767);
}

.cartInput > input{
    border: none;
    padding: 0.5vmax;
    text-align: center;
    outline: none;
    width: 1vmax;
    font: 400 0.8vmax "Roboto";
    color: rgba(0,0,0,0.74);
}

.cartSubTotal{
    display: flex;
    padding: 0.5vmax;
    height: 8vmax;
    align-items: center;
    box-sizing: border-box;
    font: 300 1vmax cursive;
    justify-content: flex-end;
    color: rgba(0,0,0,0.753);
}

.cartGrossProfit{
    display: grid;
    grid-template-columns: 2fr 1.2fr;
}

.cartGrossProfitBox{
    border-top: 3px solid tomato;
    margin: 1vmax 4vmax;
    box-sizing: border-box;
    padding: 2vmax 0;
    font: 300 1vmax "Roboto";
    display: flex;
    justify-content: space-between;
}

.checkOutBtn{
    display: flex;
    justify-content: flex-end;
}

.checkOutBtn > button{
    background-color: tomato;
    color: white;
    border: none;
    padding: 0.8vmax 4vmax;
    width: 50%;
    font: 300 0.8vmax "Roboto";
    margin: 1vmax 4vmax;
    cursor: pointer;
    border-radius: 30px;
}

@media screen and (max-width: 600px){
    .cartPage{
        padding: 0;
        min-height: 60vh;
    }

    .cartHeader{
        width: 100%;
        font: 300 1.7vmax "Roboto";
        grid-template-columns: 3fr 1fr 1fr;
    }

    .cartContainer{
        width: 100%;
        grid-template-columns: 3fr 1fr 1fr;
    }

    .cartInput{
        height: 20vmax;
    }

    .cartInput > button{
        padding: 1.5vmax;
    }

    .cartInput > input{
        width: 2vmax;
        padding: 1.5vmax;
        font: 400 1.8vmax "Roboto";
    }

    .cartSubTotal{
        padding: 1.5vmax;
        height: 20vmax;
        font: 300 2vmax "Roboto";
    }

    .cartGrossProfit{
        display: grid;
        grid-template-columns: 0fr 2fr;
    }

    .cartGrossProfitBox{
        padding: 2vmax;
        font: 300 2vmax "Roboto";
    }

    .checkOutBtn > button{
        padding: 2vmax 4vmax;
        width: 100%;
        font: 300 2vmax "Roboto";
    }
}