.paymentContainer{
    display: grid;
    place-items: center;
    background-color: white;
    height: 65vh;
    margin: 2vmax;
}

.paymentForm{
    width: 22%;
    height: 100%;
}

.paymentForm > p{
    font: 400 2vmax "Roboto";
    color: rgba(0,0,0,0.753);
    border-bottom: 2px solid rgba(27, 5, 5, 0.13);
    text-align: center;
    width: 50%;
    margin: auto;
    padding: 1vmax 0;
}

.paymentForm > div{
    display: flex;
    align-items: center;
    margin: 2vmax 0;
}

.paymentInput{
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0,0,0,0.267);
    border-radius: 4px;
    outline: none;
}

.paymentForm > div > svg{
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0,0,0,0.623);
}

.paymentForBtn{
    border: 1px solid black;
    background-color: white;
    color: black;
    font: 300 0.9vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    outline: none;
}

.paymentForBtn:hover{
    background-color: tomato;
    color: white;
}

@media screen and (max-width: 600px){
    .paymentForm{
        width: 90%;
    }

    .paymentForm > p{
        font: 400 8vw "Roboto";
        padding: 4vw 0;
        width: 60%;
    }

    .paymentForm > div{
        display: flex;
        align-items: center;
        margin: 10vw 0;
    }

    .paymentInput{
        padding: 4vw 10vw;
    }

    .paymentForm > div > svg{
        font-size: 6vw;
    }

    .paymentForBtn{
        font: 300 4vw "Roboto";
        padding: 4vw;
    }
}